<template>
	<view class="contacts">
		<view class="contacts-container">
			<view class="user-list">
				<view class="user-list-item" v-for="(group, id) in groups" :key="id" @click="groupChat(group)">
					<view class="user-item-avatar">
						<image :src="group.avatar" />
					</view>
					<view class="user-item-info">
						<span class="user-item-info__name">{{ group.name }}</span>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	const GoEasy = uni.$GoEasy;
	export default {
		name: 'contacts',
		data() {
			return {
				groups: [],
				currentUser:''
			}
		},
		onShow() {
			this.currentUser = uni.getStorageSync('currentUser');
			if (GoEasy.getConnectionStatus() === 'disconnected') {
				this.connectGoEasy(); //连接goeasy
				this.subscribeGroup(); //建立连接后，就应该订阅群聊消息，避免漏掉
			}
			this.getMyGroupChat()
			uni.setNavigationBarTitle({
				title:'我的群聊'
			})
		},
		methods: {
			connectGoEasy() {
				GoEasy.connect({
					id: this.currentUser.userId.toString(),
					data: {
						name: this.currentUser.username,
						avatar: this.currentUser.avatar
					},
					onSuccess: () => {
						console.log('GoEasy connect successfully.')
					},
					onFailed: (error) => {
						console.log('Failed to connect GoEasy, code:' + error.code + ',error:' + error
							.content);
					},
					onProgress: (attempts) => {
						console.log('GoEasy is connecting', attempts);
					}
				});
			},
			async subscribeGroup() {
				const {data} =await uni.$http.get('/groupChat/getGroupChatInfo/'+this.currentUser.userId)
				if(data.code===1){
				  this.groupIds=data.data
				}else{
					uni.$showMsg('获取群聊信息失败！')
				}
				GoEasy.im.subscribeGroup({
					groupIds: this.groupIds,
					onSuccess: function() {
						console.log('订阅群消息成功');
					},
					onFailed: function(error) {
						console.log('订阅群消息失败:', error);
					}
				});
			},
		async getMyGroupChat(){
				const {data} =await uni.$http.get('/groupChat/getMyGroupChat/'+uni.getStorageSync('userId'))
				if(data.code===1){
				  this.groups=data.data
				}else{
					uni.$showMsg(data.msg)
				}
			},
			groupChat(group) {
				uni.navigateTo({
					url: '/messagePages/groupChat?to=' + group.id+"&name="+group.name+'&avatar='+group.avatar
				});
			}
		}
	}
</script>

<style>
	.contacts {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
	}d

	.contacts .contacts-container {
		width: 100%;
		height: 100%;
		overflow: auto;
	}

	.contacts .user-list-item {
		height: 132rpx;
		padding-left: 32rpx;
		display: flex;
		align-items: center;
	}

	.contacts .contacts-title {
		height: 80rpx;
		line-height: 100rpx;
		font-size: 34rpx;
		color: #666666;
		background: #F3F4F7;
		text-indent: 44rpx;
	}

	.contacts .user-list {
		flex-grow: 1;
		background: #ffffff;
		display: flex;
		flex-direction: column;
	}

	.contacts .user-item-avatar {
		width: 96rpx;
		height: 96rpx;
		margin-right: 32rpx;
		overflow: hidden;
		position: relative;
	}

	.contacts .user-item-avatar image {
		width: 100%;
		height: 100%;
		display: block;
	}

	.contacts .user-item-info {
		height: 130rpx;
		padding-right: 32rpx;
		line-height: 88rpx;
		flex-grow: 1;
		border-bottom: 1px solid #EFEFEF;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.contacts .user-item-info__name {
		font-size: 34rpx;
		font-style: normal;
		font-weight: bold;
		color: #262628;
	}
</style>